<template>
    <div class="CompCssWAgGridVue" v-bind:ch_param="ch_param">

        <div v-bind:style="{height:height+'px'}">

            <ag-grid-vue
                style="width:100%; height:100%;"
                class="ag-theme-balham"
                v-bind:columnDefs="columns"
                v-bind:rowData="filterRows"
                v-bind:rowStyle="getRowsStyle"
                v-bind:gridOptions="gridOptions"
                v-on:cellClicked="agCellClick"
                v-on:cellDoubleClicked="agCellDbClick"
                v-on:cellValueChanged="agCellChange"
                v-on:grid-ready="onGridReady"
            ></ag-grid-vue>

        </div>

    </div>
</template>

<script>
import { AgGridVue } from 'ag-grid-vue' //vue-class-componentvue-property-decorator

import map from 'lodash/map'
import each from 'lodash/each'
import filter from 'lodash/filter'
import merge from 'lodash/merge'
import delay from 'lodash/delay'
import join from 'lodash/join'
import values from 'lodash/values'
import cloneDeep from 'lodash/cloneDeep'
import difference from 'lodash/difference'
import concat from 'lodash/concat'

import haskey from 'wsemi/src/haskey.mjs'
import isobj from 'wsemi/src/isobj.mjs'
import isnum from 'wsemi/src/isnum.mjs'
import isfun from 'wsemi/src/isfun.mjs'
import isbol from 'wsemi/src/isbol.mjs'
import binstr from 'wsemi/src/binstr.mjs'
import getdtv from 'wsemi/src/getdtv.mjs'
import str2md5 from 'wsemi/src/str2md5.mjs'
import oo from 'wsemi/src/oo.mjs'
import onTooltip from 'wsemi/src/onTooltip.mjs'


//tooltip, keymsghtml
let dtmsg = {}
window.ttWAgGridVue = function(ele, kmsg) {
    let msg = dtmsg[kmsg]
    onTooltip(ele, msg)
}


/**
 * @vue-prop {Object} opt 
 * @vue-prop {Array} opt.keys keys
 * @vue-prop {Array} opt.rows keys[{},{},...,{}]
 * @vue-prop {Object} [opt.kpHead={}] keyheadkeys
 * @vue-prop {Object} [opt.kpHeadTooltip={}] keytooltiphtmlhead{}
 * @vue-prop {Object} [opt.kpHeadAlighH={}] keyheadkeycenter
 * @vue-prop {Object} [opt.kpHeadSort={}] keyheadkeytrue
 * @vue-prop {Object} [opt.kpHeadFix={}] keyheadkeyfalse
 * @vue-prop {Object} [opt.kpHeadFilter={}] keyheadkeytrue
 * @vue-prop {Object} [opt.kpRowStyle={}] keyrow stylekeycellrow style{}
 * @vue-prop {Number} [opt.defCellMinWidth=null] cellnull
 * @vue-prop {Object} [opt.kpRowDrag={}] [:ag-grid] keyrowkeyfalse
 * @vue-prop {Object} [opt.kpCellWidth=null] keycellkeynull
 * @vue-prop {Object} [opt.kpCellRender={}] keycell{}
 * @vue-prop {Object} [opt.kpCellTooltip={}] keycelltooltiphtmlcell{}
 * @vue-prop {Object} [opt.kpCellAlighH={}] keycellkeycenter
 * @vue-prop {Object} [opt.kpCellEditable={}] keycellkeyfalse
 * @vue-prop {Function} [opt.rowClick={}] row clickfunction(){}
 * @vue-prop {Function} [opt.rowDbClick={}] row double clickfunction(){}
 * @vue-prop {Function} [opt.rowChange={}] row changefunction(){}
 * @vue-prop {Function} [opt.cellClick={}] cell clickfunction(){}
 * @vue-prop {Function} [opt.cellDbClick={}] cell double clickfunction(){}
 * @vue-prop {Function} [opt.cellChange={}] cell changefunction(){}
 * @vue-prop {Number} [height=300] 300(px)
 * @vue-prop {String} [search=''] ''
 * @vue-event {Null} refresh 
 * @vue-event {Array} showKeys keyskeys
 * @vue-event {Null} getDisplayData 
 */
export default {
    components: { AgGridVue }, //ag-grid-vue, udmag-grid-vue, 
    props: {
        opt: {
            type: Object,
        },
        height: {
            type: Number,
            default: 300
        },
        search: {
            type: String,
            default: ''
        },
    },
    data: function() {
        let vo = this
        return {

            canFitColumn: true,

            rows: [],
            keys: [],
            columns: [],

            kpHead: {},
            kpHeadTooltip: {},
            kpHeadAlighH: {},
            kpHeadSort: {},
            kpHeadFix: {},
            kpHeadFilter: {},
            kpRowStyle: {},
            defCellMinWidth: null,
            kpRowDrag: {}, //ag-grid
            kpCellWidth: {},
            kpCellRender: {},
            kpCellTooltip: {},
            kpCellAlighH: {},
            kpCellEditable: {},

            tableClickEnable: false,
            rowChange: function() {},
            rowClick: function() {},
            rowDbClick: function() {},
            cellClick: function() {},
            cellDbClick: function() {},
            cellChange: function() {},

            gridOptions: {
                animateRows: true,
                floatingFilter: true,
                rowDragManaged: true,
                singleClickEdit: true, //
                localeText: { noRowsToShow: '' },
                getRowStyle: vo.agGetRowStyle,
            },

            ag: null,

        }
    },
    mounted: function() {
    },
    computed: {

        ch_param: function() {
            //console.log('computed ch_param')

            let vo = this

            //opt for trigger
            let opt = vo.opt

            //ch_opt
            vo.ch_opt()

            return {
                opt: opt
            }
        },

        getRowsStyle: function(params) {
            //console.log('computed getRowsStyle', params)

            let vo = this

            if (vo.tableClickEnable) {
                return {
                    cursor: 'pointer'
                }
            }
            return {}
        },

        filterRows: function() {
            //console.log('computed filterRows')

            let vo = this

            //rows, search for trigger
            let rows = vo.rows
            let search = vo.search

            //check
            if (search === '') {
                return rows
            }

            //filter
            // let frs = _.chain(rows)
            //     .filter(function(v, k) {
            //         let c = _.join(_.values(v), '')
            //         return binstr(c, search)
            //     })
            //     .value()
            let frs = filter(rows, function(v, k) {
                let c = join(values(v), '')
                return binstr(c, search)
            })

            return frs
        },

    },
    methods: {

        agGetRowStyle: function(params) {
            //console.log('agGetRowStyle', params) //row

            let vo = this

            //style
            let style = {}
            each(vo.kpRowStyle, function(v, k) {
                let f = v
                let d = getdtv(params.data, k)
                if (isfun(f) && d) {
                    style = merge(style, f(d))
                }
            })

            return style
        },

        agCellClick: function(param) {
            //console.log('methods agCellClick', param)

            let vo = this

            //cellClick
            vo.cellClick(param.colDef.field, param.data)

            //rowClick
            vo.rowClick(param.colDef.field, param.data)

        },

        agCellDbClick: function(param) {
            //console.log('methods agCellDbClick', param)

            let vo = this

            //cellDbClick
            vo.cellDbClick(param.colDef.field, param.data)

            //rowDbClick
            vo.rowDbClick(param.colDef.field, param.data)

        },

        agCellChange: function(param) {
            //console.log('methods agCellChange', param)

            let vo = this

            //check
            if (param.oldValue !== param.newValue) {

                //refresh
                vo.refresh()

                //cellChange
                vo.cellChange(param.colDef.field, param.data)

                //rowChange
                vo.rowChange(param.colDef.field, param.data)

            }

        },

        refresh: function() {
            //console.log('methods refresh')

            let vo = this

            //delay, 
            delay(function() {

                //redrawRows, getRowStyle, redrawRows
                vo.gridOptions.api.redrawRows()

            }, 1)

        },

        ch_opt: function() {
            //console.log('methods ch_opt')

            let vo = this

            //setobj
            function setobj(keys, defEvalKey = true, def, kpobj) {
                let o = {}
                each(keys, function(key) {
                    let h
                    if (defEvalKey) {
                        h = def(key)
                    }
                    else {
                        h = def
                    }
                    if (isobj(kpobj)) {
                        if (haskey(kpobj, key)) {
                            h = kpobj[key]
                        }
                    }
                    o[key] = h
                })
                return o
            }

            //canFitColumn, ag-grid
            let canFitColumn = false
            if (isbol(vo.opt.canFitColumn)) {
                canFitColumn = vo.opt.canFitColumn
            }
            vo.canFitColumn = canFitColumn

            //keys
            vo.keys = vo.opt.keys

            //kpHead
            vo.kpHead = setobj(vo.keys,
                true,
                function(key) {
                    return key //keyhead
                },
                vo.opt.kpHead
            )

            //kpHeadTooltip
            vo.kpHeadTooltip = {}
            if (isobj(vo.opt.kpHeadTooltip)) {
                vo.kpHeadTooltip = vo.opt.kpHeadTooltip
            }

            //kpHeadAlighH
            vo.kpHeadAlighH = setobj(vo.keys,
                true,
                function(key) {
                    return 'center' //center
                },
                vo.opt.kpHeadAlighH
            )

            //kpHeadSort
            vo.kpHeadSort = setobj(vo.keys,
                true,
                function(key) {
                    return true //true
                },
                vo.opt.kpHeadSort
            )

            //kpHeadFix
            vo.kpHeadFix = setobj(vo.keys,
                true,
                function(key) {
                    return false //false
                },
                vo.opt.kpHeadFix
            )

            //kpHeadFilter
            vo.kpHeadFilter = setobj(vo.keys,
                true,
                function(key) {
                    return true //true
                },
                vo.opt.kpHeadFilter
            )

            //kpRowStyle
            vo.kpRowStyle = {}
            if (isobj(vo.opt.kpRowStyle)) {
                vo.kpRowStyle = vo.opt.kpRowStyle
            }

            //kpRowDrag, ag-grid
            vo.kpRowDrag = setobj(vo.keys,
                true,
                function(key) {
                    return false //false
                },
                vo.opt.kpRowDrag
            )

            //defCellMinWidth
            vo.defCellMinWidth = null
            if (isnum(vo.opt.defCellMinWidth)) {
                vo.defCellMinWidth = vo.opt.defCellMinWidth
            }

            //kpCellWidth
            vo.kpCellWidth = setobj(vo.keys,
                true,
                function(key) {
                    return null //null
                },
                vo.opt.kpCellWidth
            )

            //kpCellRender
            vo.kpCellRender = {}
            if (isobj(vo.opt.kpCellRender)) {
                vo.kpCellRender = vo.opt.kpCellRender
            }

            //kpCellTooltip
            vo.kpCellTooltip = {}
            if (isobj(vo.opt.kpCellTooltip)) {
                vo.kpCellTooltip = vo.opt.kpCellTooltip
            }

            //kpCellAlighH
            vo.kpCellAlighH = setobj(vo.keys,
                true,
                function(key) {
                    return 'center' //center
                },
                vo.opt.kpCellAlighH
            )

            //kpCellEditable
            vo.kpCellEditable = setobj(vo.keys,
                true,
                function(key) {
                    return false //false
                },
                vo.opt.kpCellEditable
            )

            //tableClickEnable
            vo.tableClickEnable = false

            //rowClick
            vo.rowClick = function() {}
            if (isfun(vo.opt.rowClick)) {
                vo.rowClick = vo.opt.rowClick
                vo.tableClickEnable = true
            }

            //rowDbClick
            vo.rowDbClick = function() {}
            if (isfun(vo.opt.rowDbClick)) {
                vo.rowDbClick = vo.opt.rowDbClick
                vo.tableClickEnable = true
            }

            //rowChange
            vo.rowChange = function() {}
            if (isfun(vo.opt.rowChange)) {
                vo.rowChange = vo.opt.rowChange
            }

            //cellClick
            vo.cellClick = function() {}
            if (isfun(vo.opt.cellClick)) {
                vo.cellClick = vo.opt.cellClick
                vo.tableClickEnable = true
            }

            //cellDbClick
            vo.cellDbClick = function() {}
            if (isfun(vo.opt.cellDbClick)) {
                vo.cellDbClick = vo.opt.cellDbClick
                vo.tableClickEnable = true
            }

            //cellChange
            vo.cellChange = function() {}
            if (isfun(vo.opt.cellChange)) {
                vo.cellChange = vo.opt.cellChange
            }

            //columns, rows, rowscolumn
            vo.columns = vo.keys2columns(vo.keys)

            //rows
            vo.rows = vo.opt.rows

        },

        keys2columns: function(keys) {
            //console.log('methods keys2columns')

            let vo = this

            let r = map(keys, function(key) {
                let o = {}

                //key
                o.field = key

                //text
                o.headerName = vo.kpHead[key]

                //sort
                o.sortable = vo.kpHeadSort[key]

                //filter
                o.filter = vo.kpHeadFilter[key] //, 'agTextColumnFilter'
                //o.suppressMenu = true //

                //filterParams
                o.filterParams = {
                    // caseSensitive: true, //, 
                    // applyButton: true, //
                    // clearButton: true, //
                    // filterParams: {
                    //     comparator: function(ft, cv) {
                    //         console.log('comparator', ft, cv)
                    //     }
                    // }
                }

                //filter plus, , gridOptionsfloatingFilter=true
                o.floatingFilterComponentParams = { suppressFilterButton: true } //, suppressFilterButton=true

                //resizable
                o.resizable = true

                //editable
                o.editable = vo.kpCellEditable[key]

                //funHeadTooltip
                let funHeadTooltip = vo.kpHeadTooltip[key]

                //header onmouseenter
                let headerMouseenter = ''
                if (isfun(funHeadTooltip)) {

                    //tooltip
                    let t = funHeadTooltip(vo.kpHead[key])

                    //kmsg
                    let kmsg = str2md5(t)

                    //add dtmsg
                    dtmsg[kmsg] = t

                    //onmouseenter
                    headerMouseenter = `onmouseenter="ttWAgGridVue(this,'${kmsg}')"`

                }

                //text-align
                let cTextAlign = vo.kpHeadAlighH[key]

                //justify-content
                let kpTA2JC = {
                    'left': 'flex-start',
                    'center': 'center',
                    'right': 'flex-end',
                }
                let cJustifyContent = kpTA2JC[cTextAlign]

                //header template
                o.headerComponentParams = {
                    template: `
                        <div class="ag-cell-label-container" role="presentation"" ${headerMouseenter}>
                            <span ref="eMenu" class="ag-header-icon ag-header-cell-menu-button"></span>
                            <div ref="eLabel" class="ag-header-cell-label" style="justify-content:${cJustifyContent};" role="presentation">
                                <span ref="eText" class="ag-header-cell-text" role="columnheader"></span>
                                <span ref="eSortOrder" class="ag-header-icon ag-sort-order" ></span>
                                <span ref="eSortAsc" class="ag-header-icon ag-sort-ascending-icon" ></span>
                                <span ref="eSortDesc" class="ag-header-icon ag-sort-descending-icon" ></span>
                                <span ref="eSortNone" class="ag-header-icon ag-sort-none-icon" ></span>
                                <span ref="eFilter" class="ag-header-icon ag-filter-icon"></span>
                            </div> 
                        </div>
                    `
                }

                //funCellTooltip
                let funCellTooltip = vo.kpCellTooltip[key]

                //funCellRender
                let funCellRender = vo.kpCellRender[key]

                //render
                o.cellRenderer = function(params) {
                    //console.log('cellRenderer', params)

                    //funCellTooltip
                    if (isfun(funCellTooltip)) {

                        //tooltip
                        let t = funCellTooltip(params.value)

                        //kmsg
                        let kmsg = str2md5(t)

                        //add dtmsg
                        dtmsg[kmsg] = t

                        //onmouseenter
                        params.eGridCell.setAttribute('onmouseenter', `ttWAgGridVue(this,'${kmsg}')`)

                    }

                    //h
                    let h = params.value

                    //funCellRender
                    if (isfun(funCellRender)) {
                        h = funCellRender(params.value, oo(params.data))
                    }

                    return h
                }

                //selectable
                o.checkboxSelection = false

                //fix
                if (vo.kpHeadFix[key]) {

                    //pinned
                    o.pinned = vo.kpHeadFix[key] ? 'left' : ''

                    //suppressSizeToFit, sizeColumnsToFit
                    o.suppressSizeToFit = true

                }

                //align
                o.cellStyle = {
                    'text-align': vo.kpCellAlighH[key],
                }

                //width, width
                if (isnum(vo.kpCellWidth[key])) {

                    //width
                    o.width = vo.kpCellWidth[key]

                }

                //min-width, ag-gridminWidth, minWidth150, width200, width
                if (isnum(vo.defCellMinWidth)) {

                    //min-width
                    o.minWidth = vo.defCellMinWidth

                }

                //rowDrag
                o.rowDrag = vo.kpRowDrag[key]

                //hide
                o.hide = false

                return o
            })

            return r
        },

        onGridReady: function(params) {
            //console.log('methods onGridReady', params)

            let vo = this

            //ag
            vo.ag = params

            if (vo.canFitColumn) {

                //delay
                delay(function() {
                    params.api.sizeColumnsToFit()
                }, 1)

            }

        },

        showKeys: function(keys_new) {
            //console.log('methods showKeys', keys_new)

            let vo = this

            //keys_old
            let keys_old = cloneDeep(vo.keys)

            //keys_nouse
            let keys_nouse = difference(keys_old, keys_new)

            //cls_new
            let cls_new = vo.keys2columns(keys_new)
            let cls_nouse = vo.keys2columns(keys_nouse)
            cls_nouse = map(cls_nouse, function(cl) {
                cl.hide = true //column
                return cl
            })

            //cls
            let cls = concat(cls_new, cls_nouse)

            //update
            vo.gridOptions.api.setColumnDefs(cls)

        },

        getDisplayData: function() {
            //console.log('methods getDisplayData')

            let vo = this

            //rs
            let rs = []
            vo.gridOptions.api.forEachNodeAfterFilterAndSort(function(node) {
                rs.push(oo(node.data))
            })

            return rs
        },

    },
}
</script>

<style>
.CompCssWAgGridVue .ag-theme-balham, .ag-header {
    font-family: inherit;
}
.CompCssWAgGridVue .ag-theme-balham .ag-header {
    font-family: inherit;
}
.CompCssWAgGridVue .ag-floating-filter-input, .ag-filter-filter, .ag-filter-select {
    transition: all 1s;
    padding: 3px;
    min-height: 21px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 4px;
}
.CompCssWAgGridVue .ag-floating-filter-input:focus, .ag-filter-filter:focus, .ag-filter-select:focus {
    border: 1px solid #777;
}
.CompCssWAgGridVue .ag-floating-filter-button {
    margin: 0 !important;
    line-height: inherit !important;
    vertical-align: middle !important;
}
.CompCssWAgGridVue .ag-icon {
    vertical-align: middle !important;
}
</style>
